.trip{
  margin:4rem 6rem;
  color:#2a2a2a;
}

.trip h1{
  font-size:3rem;
}

.tripcard{
  margin-top:3rem;
  display:flex;
  justify-content:space-between;
}

.t-card{
  width:32%;
  text-align:start;
  box-shadow:0 5px 25px 2px rgba(0,0,0,0.15);
  border-radius:7px;
  padding:1rem 0.8rem;
  cursor:pointer;
}

.card-img{
  height:200px;
  overflow:hidden;
  border-radius:7px;
}

.card-img img{
  width:100%;
  height:100%;
  transition:0.3s ease-in-out;
  border-radius:7px;
  object-fit:cover;
}

.card-img:hover img{
  transform:scale(1.3);
}

.t-card h3{
  font-size:1.3rem;
  padding:0.9rem 0.2rem 0;
}

@media screen and (max-width:965px){
  .trip{
    margin:4rem 2rem;
  }
  
  .tripcard{
    display:flex;
    flex-direction:column;
  }

  .t-card{
    width:100%;
    margin-bottom:1.5rem;
  }
  
}